*{
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}
.control{
	z-index: 1;
	position: absolute;
}
.page_btn{
	border: none;
    cursor: pointer;
    background-color: transparent;
	 color: white;
	 outline: none;
	 -webkit-user-select: none;
	    -moz-user-select: none;
	     -ms-user-select: none;
	         user-select: none;
	 font-size: 3vmin;
    /* border-bottom: 3px solid blue; */
    padding: 5px 5px;
    margin: 5px 10px 5px 10px;
}
.active{
	border-bottom: 3px solid blue;
	color: white;
}
.non_active{
	border: none;
	color: rgba(255, 255, 255, 0.5);
}
/***********************************************************
 *                         PAGE 1                          *
 ***********************************************************/

 .page1{
	 background-color: black;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	width: 100%;
	min-height: 100vh;
	position: absolute;
	top: 0;
	left: 0;
	overflow:hidden;
}
.rec{
	width: 80vmin;
	height: 20vmin;
	background-color: #fff;
	border-radius: 3%;
	position: relative;
	-webkit-animation: recAni 2s ease-in-out infinite ;
	        animation: recAni 2s ease-in-out infinite ;
}
.rec:hover .box{
	-webkit-animation-play-state: paused;
	        animation-play-state: paused;
	cursor: not-allowed;
}
.box{
	width: 5vmin;
	height: 5vmin;
	background-color: green;
	position: absolute;
	left: 0%;
	top: calc(50% - 2.5vmin);
	-webkit-animation: boxAni 1s ease-in-out 0s infinite alternate;
	        animation: boxAni 1s ease-in-out 0s infinite alternate;

}

@-webkit-keyframes recAni{
	0%, 100%{
		-webkit-transform: rotate(0deg) ;
		        transform: rotate(0deg) ;
		
	}
	50%{
		-webkit-transform: rotate(45deg) ;
		        transform: rotate(45deg) ;

	}
}

@keyframes recAni{
	0%, 100%{
		-webkit-transform: rotate(0deg) ;
		        transform: rotate(0deg) ;
		
	}
	50%{
		-webkit-transform: rotate(45deg) ;
		        transform: rotate(45deg) ;

	}
}

@-webkit-keyframes boxAni{
	0%{
		left: 0%;
		border-radius: 5px;
	}
	50%{
		border-radius: 50%;
		width:10vmin;
		height: 10vmin;

	}
	100%{
		width:10vmin;
		height: 10vmin;
		top: calc(50% - 5vmin);
		left: calc(100% - 10vmin);
		background-color: red;
		border-radius: 5px;
	}
}

@keyframes boxAni{
	0%{
		left: 0%;
		border-radius: 5px;
	}
	50%{
		border-radius: 50%;
		width:10vmin;
		height: 10vmin;

	}
	100%{
		width:10vmin;
		height: 10vmin;
		top: calc(50% - 5vmin);
		left: calc(100% - 10vmin);
		background-color: red;
		border-radius: 5px;
	}
}



/***********************************************************
 *                          PAGE2                          *
 ***********************************************************/
 
 .page2{
	background-color: black;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	width: 100%;
	min-height: 100vh;
	position: absolute;
	top: 0;
	left: 0;
	overflow:hidden;

}
.title{
	position: absolute;
	top:3px;
	left:3px;
	color: white;
}
.container{
	width: 47vmin;
	height: 5vmin;
	/* border: 2px solid green; */
	position: absolute;
	left: 50%;
	top: calc(50% - 2.5vmin);
	-webkit-transform-origin: 0% 50%;
	    -ms-transform-origin: 0% 50%;
	        transform-origin: 0% 50%;
}
.ball{
	width: 0vmin;
	height: 0vmin;
	border-radius: 50%;
	background-color: red;
	position: absolute;
	top: calc(50% - 0.5vmin);
	-webkit-transform: translateY(43vmin);
	    -ms-transform: translateY(43vmin);
	        transform: translateY(43vmin);
	-webkit-animation: ball_ani 2s ease-in-out  0s infinite alternate;
	        animation: ball_ani 2s ease-in-out  0s infinite alternate;
	-webkit-animation-fill-mode: both;
	        animation-fill-mode: both;
}
@-webkit-keyframes ball_ani{
	0%{
		width: 0vmin;
		height: 0vmin;
		-webkit-transform: translateX(0);
		        transform: translateX(0);
		background-color: white;
	}
	100%{
		width: 5vmin;
		height: 5vmin;
		background-color: red;
		-webkit-transform: translateX(43vmin);
		        transform: translateX(43vmin);

	}
}
@keyframes ball_ani{
	0%{
		width: 0vmin;
		height: 0vmin;
		-webkit-transform: translateX(0);
		        transform: translateX(0);
		background-color: white;
	}
	100%{
		width: 5vmin;
		height: 5vmin;
		background-color: red;
		-webkit-transform: translateX(43vmin);
		        transform: translateX(43vmin);

	}
}



